.search_ctn {
  display: flex;
  max-width: 100vw;
  height: calc(100vh - 60px);
  /* margin: 3.7rem 0; */
  padding: 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;

  background-image: url("../../../public/imgs/conseillere.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.search_ctn > h1 {
  font-size: 3rem;
  color: white;
  letter-spacing: 0.2rem;
  font-weight: 100;
  text-align: center;
  opacity: 0;
}

.search_ctn > h1 > span {
  font-weight: 900;
}

.search_form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: 0.8rem;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 2rem;
}

.search_form #select-ctn {
  width: 16.5rem;
  display: flex;
  gap: 0.7rem;
  padding: 0;
  margin: 0;
  height: 3rem;
}

.search_form #select-ctn select {
  border-radius: 5px;
  width: 50%;
}

.search_form .search_form-item {
  height: 3rem;
  border-radius: 5px;
  padding-left: 0.7rem;
  border: none;
  outline: none;
}

.search_form .search_form-item:hover {
  cursor: pointer;
}

.search_form .select-ctn,
.search_input {
  width: 20rem;
}

.submit-btn {
  width: 10rem;
  background: var(--primary);
  color: white;
  font-size: 1rem;
  border: 1px solid;
}

.submit-btn:hover {
  background: white;
  color: var(--primary);
  /* border: none; */
}

/*styliser les balises options d'une balise select*/

@media only screen and (max-width: 992px) {
  .search_ctn #submit-btn {
    width: 16.5rem;
  }
}

@media only screen and (max-width: 668px) {
  .search_ctn .search_form {
    justify-content: center;
  }
  .search_ctn .search_input {
    width: 16.5rem;
  }
  .search_ctn #submit-btn {
    margin-top: 0;
  }
}
